import {
  Streamlit,
  StreamlitComponentBase,
  withStreamlitConnection,
} from "streamlit-component-lib"
import React, { useState, ReactNode } from "react"
import "./styles.scss"

interface State {
  value: string
  isListening: boolean
  isActive: boolean
}

/**
 * This is a React-based component template. The `render()` function is called
 * automatically when your component should be re-rendered.
 */
class SearchBarComponent extends StreamlitComponentBase<State> {
  public render = (): ReactNode => {    
    const SpeechRecognition = (window as any).webkitSpeechRecognition;
    const speech = new SpeechRecognition();
    speech.interimResults = true;

    speech.onresult = (event: any) => {
      const text = Array.from(event.results)
          .map((result: any) => result[0])
          .map((result: any) => result.transcript)
          .join('');

      this.setState({value: text});
    };

    speech.onend = () => {
      this.onSearch();
      this.setState({isListening: false})
    }

    const listen = () => {
      if (!this.state?.isListening) {
        speech.start();
      }
      this.setState({isListening: true})
    };

    const subtext = this.props.args["subtext"]
    const suggestions = this.props.args["suggestions"]
    const input = this.props.args["value"]
    const textwidth: string = this.props.args["width"]

    if (input) {
      this.setState({value: input});
    }

    return (
      <span>
        <div className="search-form">
          <input type="text" onFocus={this.onFocus} onBlur={this.onBlur} 
                value={this.state?.value} placeholder={subtext} 
                onKeyPress={this._handleKeyDown} onChange={this.onChange}
                className="search-input" style={{width: textwidth}}></input>

          <button type="button" className="search-button" onClick={this.onSearch}>
            <svg className="submit-button">
              <use xmlnsXlink="http://www.w3.org/1999/xlink" xlinkHref="#search"></use>
            </svg>
          </button>
          <div className="search-option">
            <div>
              <button type="button" onFocus={this.onFocus}
                      onBlur={this.onBlur} onClick={listen}>
                <svg id="record-button" className={this.state?.isListening ? "recording" : ""}>
                  <use xmlnsXlink="http://www.w3.org/1999/xlink" xlinkHref="#user"></use>
                </svg>
              </button>
            </div>
          </div>
          <div className="dropdown" style={{backgroundColor: 'white', 
                                            boxShadow: '0 6px 8px rgba(0, 0, 0, 0.15)'}}>
            <div className="scroll" style={{maxHeight: (this.state?.isActive ? 100 : 'initial'),
                                            overflowY: (this.state?.isActive ? 'scroll' : 'hidden')}}>
              {
                suggestions?.filter((item: string) => {
                  if (!this.state?.isActive) {
                    return '';
                  } else if (this.state?.value != null && this.state.value != "") {
                    const searchTerm = this.state.value.toLowerCase();
                    const suggestion = item.toLowerCase();

                    return searchTerm && suggestion.startsWith(searchTerm)
                          && suggestion !== searchTerm;
                  } else {
                    return item;
                  }
                })
                .map((suggestion: string) => 
                  <div className="dropdown-row" 
                        onMouseDown={() => this.setSuggestion(suggestion)}>
                          {suggestion}
                  </div>)
              }
            </div>
          </div>
          <div className="dropdown">&nbsp;</div>
        </div>

        <svg xmlns="http://www.w3.org/2000/svg" width="0" height="0" display="none">
          <symbol id="search" viewBox="0 0 32 32">
            <path d="M 19.5 3 C 14.26514 3 10 7.2651394 10 12.5 C 10 14.749977 10.810825 16.807458 12.125 18.4375 L 3.28125 27.28125 L 4.71875 28.71875 L 13.5625 19.875 C 15.192542 21.189175 17.250023 22 19.5 22 C 24.73486 22 29 17.73486 29 12.5 C 29 7.2651394 24.73486 3 19.5 3 z M 19.5 5 C 23.65398 5 27 8.3460198 27 12.5 C 27 16.65398 23.65398 20 19.5 20 C 15.34602 20 12 16.65398 12 12.5 C 12 8.3460198 15.34602 5 19.5 5 z" />
          </symbol>
          <symbol id="user" viewBox="0 0 18 18">
            <path d="M3.5 6.5A.5.5 0 0 1 4 7v1a4 4 0 0 0 8 0V7a.5.5 0 0 1 1 0v1a5 5 0 0 1-4.5 4.975V15h3a.5.5 0 0 1 0 1h-7a.5.5 0 0 1 0-1h3v-2.025A5 5 0 0 1 3 8V7a.5.5 0 0 1 .5-.5z"/>
            <path d="M10 8a2 2 0 1 1-4 0V3a2 2 0 1 1 4 0v5zM8 0a3 3 0 0 0-3 3v5a3 3 0 0 0 6 0V3a3 3 0 0 0-3-3z"/>
          </symbol>
        </svg> 
      </span>
    )
  }

  private onChange = (event: any) => {
    this.setState({value: event.target.value})
  }

  private setSuggestion = (suggestion: string) => {
    this.setState(
                  {value: suggestion},
                  () => Streamlit.setComponentValue(this.state?.value)
                 )
  }

  private onFocus = (event: any) => {
    this.setState({isActive: true});
  }

  private onBlur = (event: any) => {
    this.setState({isActive: false});
  }

  private onSearch = (): void => {
    this.setState(
      () => Streamlit.setComponentValue(this.state?.value)
    )
  }

  private _handleKeyDown = (e: React.KeyboardEvent<HTMLInputElement>) => {
    if (e.key === 'Enter') {
      this.setState(
        () => Streamlit.setComponentValue(this.state.value)
      )
    }
  }

  // /** Click handler for our "Click Me!" button. */
  // private onClicked = (): void => {
  //   // Increment state.numClicks, and pass the new value back to
  //   // Streamlit via `Streamlit.setComponentValue`.
  //   this.setState(
  //     prevState => ({ numClicks: prevState.numClicks + 1 }),
  //     () => Streamlit.setComponentValue(this.state.numClicks)
  //   )
  // }

  // /** Focus handler for our "Click Me!" button. */
  // private _onFocus = (): void => {
  //   this.setState({ isFocused: true })
  // }

  // /** Blur handler for our "Click Me!" button. */
  // private _onBlur = (): void => {
  //   this.setState({ isFocused: false })
  // }
}

// "withStreamlitConnection" is a wrapper function. It bootstraps the
// connection between your component and the Streamlit app, and handles
// passing arguments from Python -> Component.
//
// You don't need to edit withStreamlitConnection (but you're welcome to!).
export default withStreamlitConnection(SearchBarComponent)
